<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process217"></a>

<p><br></p>
<p><span class="rvts17">不是开玩笑的事情（</span><span class="rvts50">No Laughing Matter</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:477,Y:444</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：玛德伯偌（</span><span class="rvts20">Mudbarrow</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">为玛德伯偌复仇，并获得他的宝箱</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1279.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">在睡着的孤狼士兵斯兰波杰克（</span><span class="rvts20">Slumberjack</span><span class="rvts21">）身旁有一个灵魂叫做玛德伯偌，他生前由于被斯兰波杰克迫害，失去了毕生收藏的宝箱。</span>
<br><span class="rvts21">玛德伯偌想要你找到该宝箱的位置。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1280.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">通过和那个瞌睡的孤狼士兵谈话，他会告诉你宝箱就在不远处的沙滩上。如果和瞌睡的士兵交谈太久，会被锯木厂中的那个蜥蜴发现，并指控你在</span><span class="rvts20"></span><span class="rvts21">扰乱军营</span><span class="rvts20"></span><span class="rvts21">，之后便会发生战斗。</span>
<br><span class="rvts21">如果不与瞌睡的孤狼士兵交谈，也可以在海滩这个位置（</span><span class="rvts20">X:459,Y:449</span><span class="rvts21">）发现宝箱（需要</span><span class="rvts20">18</span><span class="rvts21">点的智力值），发现宝箱与玛德伯偌谈话，也可以触发该任务。</span>
<br><span class="rvts21">发现宝箱后，与玛德伯偌谈论关于如何处理宝箱的事宜，使用委婉的语气会使得他不停的大笑，最终放开世间的心结，愿意把宝箱交付于你，待他平息后该支线关闭。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



